    };
  },
  created () {
      //获取openid，正式环境下获取的微信个人信息
      this.getUserInfo();

          //测试环境下的个人信息
          // var that=this;
          // window.sessionStorage.setItem('openid', "oX0621BFlnSkN5SlghGgRvQHik60") //保存缓存openid
          // window.sessionStorage.setItem('staff', 1) //保存缓存 是否员工
          // window.sessionStorage.setItem('own', 2) //保存缓存 所属部门
          // window.sessionStorage.setItem('realname', "张某人") //保存缓存 真实姓名
          // window.sessionStorage.setItem('position', 1) //保存缓存 职务  
          // window.sessionStorage.setItem('dname', "市场部") //保存缓存 部门名称   
          // window.sessionStorage.setItem('ifgcb', 0) //保存缓存 是否工程设备科
          // window.sessionStorage.setItem('ifaqk', 0) //保存缓存 是否安全保卫科  
          
          // that.staff = 1;
          // that.ifgcb = 0;
          // that.ifaqk = 0;
          // that.position = 1;
  },
  methods: {

    getUserInfo() {
        var that=this;

         /**
         * 检测当前是否需要登录验证
         * 1. 确定当前页面是否需要登录
         */
        if (!window.sessionStorage.getItem('openid')) {
          // 微信授权
          if (!that.getUrlKey('code') || !that.getUrlKey('state')) {                       // 没有code 去授权获取code
            // 去授权
            // const URI = encodeURIComponent(window.location.href)
            var URI = ''
            var APPID = 'wxde6f061ac193e843'                        // APPID是已知的公众号里有
            //获取当前url路径，在回调地址中用
            if (window.location.href.indexOf('?') > 0) {  
              URI = encodeURIComponent(window.location.href)
            } else {
              URI = encodeURIComponent(window.location.href.split('?')[0])
            }

            console.log("地址",URL);
            //打开链接url
            var url = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + APPID + '&redirect_uri=' + URI + '&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'
            window.location.href = url
          } else {      // 有code通过code调后台接口去获取用户信息（uid、oppind等）
              
              console.log("code",that.getUrlKey('code'))
              that.axios.get(
                "/testgetUserInfo",
                {
                  params: {
                    code: that.getUrlKey('code')
                  }  
                }
              )
              .then(
                function(res) {
                  console.log("userinfo:",res.data.data,"------openid:",res.data.data.openid);
                  that.getALLinfo(res.data.data.openid); //获取信息
                 
              });
              
          }
        }else{
            
          that.getALLinfo(window.sessionStorage.getItem('openid'));
            
      }
    },

    getUrlKey (variable) {
       var query = window.location.search.substring(1);
       var vars = query.split("&");
       for (var i=0;i<vars.length;i++) {
               var pair = vars[i].split("=");
               if(pair[0] == variable){return pair[1];}
       }
       return(false);
    },

    getALLinfo(openid){
      var that=this;
      const toast = that.$createToast({
        time: 0,
        txt: '加载中'
      })
      toast.show();//显示加载提示
      that.axios.get(
        "/getAllInfo",
        {
          params: {
            openid: openid
          }  
        }
      )
      .then( res =>{
          window.sessionStorage.setItem('openid', res.data.data.openid) //保存缓存openid
          window.sessionStorage.setItem('staff', res.data.data.staff) //保存缓存 是否员工
          window.sessionStorage.setItem('own', res.data.data.own) //保存缓存 所属部门
          window.sessionStorage.setItem('realname', res.data.data.realname) //保存缓存 真实姓名
          window.sessionStorage.setItem('position', res.data.data.position) //保存缓存 职务  
          window.sessionStorage.setItem('dname', res.data.data.dname) //保存缓存 部门名称   
          window.sessionStorage.setItem('ifgcb', res.data.data.ifgcb) //保存缓存 是否工程设备科
          window.sessionStorage.setItem('ifaqk', res.data.data.ifaqk) //保存缓存 是否安全保卫科  
          
          that.staff = res.data.data.staff;
          that.ifgcb = res.data.data.ifgcb;
          that.ifaqk = res.data.data.ifaqk;
          that.position = res.data.data.position;
          toast.hide()//关闭加载提示
      })
    },


    /**点击装修 */
