<template>

    <cube-form :model="model" @validate="validateHandler" >
      <cube-form-group class="cfgf" v-if="articles.repend==0">
      
        <cube-form-item :field="fields[0]" class="cfi"></cube-form-item>
      </cube-form-group>

      <cube-form-group class="whcl" v-if="articles.repend==0">
      <cube-button type="button" @click="submitHandler" class="cbp">提交维修反馈</cube-button>
      </cube-form-group>

      <div class="content "  v-else>
        <div class="top" >
            <div class="title" >
              {{articles.over_bumen}}{{articles.over_name}}
            </div>
            <div class="cent">{{articles.time}}</div>
        </div>
        <div class="cont">
          记录反馈：{{articles.content}}
        </div>
        <!-- <div class="shb">
            <div class="title" >
            <label class="seed" > 录入时间：{{articles.time}}</label>
            </div>
        </div> -->
      </div>

    </cube-form>

</template>

<script>
export default {
  data() {
    return {
      validity: {},
      valid: undefined,
      model: {
        id:"",
      },
      fields: [
        {
          type: "textarea",
          modelKey: "measure",
          label: "维修记录",
          rules: {
            required: true
          },
          debounce: 100,
          props: {
            maxlength: 1000,
            placeholder: '请输入维修措施或反馈',
          }
        },
      ],
      articles:'',
    };
  },
  created() {
    /**接收url传过来的参数 */
    var _this=this
    var id=_this.$route.query.id;
    console.log("接收",id)
    _this.model.id=id;
    _this.getInfo(id);
  },
  methods: {
    /*** 
     * 接收信息
    */
    getInfo(id) {
      var that=this;
      that.model.id=id;
      that.axios.post( "http://wxgzh.liuzhouchuyun.com/fblist",
          this.qs.stringify({
            id: id,
          }))
        .then(
          function(res) {
            console.log("获取",res.data.data);
            that.articles=res.data.data;
        });
    },

    //表单提交
    submitHandler(e) {
      var that=this;
      //检验
      if(that.valid==false||that.model.measure==""){  
       that.$createDialog({
          type: 'alert',
          title: '必填项不能为空',
          icon: 'cubeic-wrong',
          confirmBtn: {
            text: '确定',
          }
        }).show() 
        return false;
      }
      this.axios.post(
          "http://wxgzh.liuzhouchuyun.com/feedback",
          this.qs.stringify({
            content: this.model.measure,
            id: this.model.id,
            over_openid:window.sessionStorage.getItem('openid'),
            over_bumen:window.sessionStorage.getItem('own'), //缓存的所属部门
            over_name:window.sessionStorage.getItem('realname') //缓存的真实姓名
          }))
        .then(
          function(res) {
            console.log(res.data);
            //控制台打印请求成功时返回的数据
            if(res.data.code==200){
              that.$createDialog({
                type: 'alert',
                title: '录入成功',
                icon: 'cubeic-right',
                confirmBtn: {
                  text: '确定',
                }
              }).show()
            }else{
              const toast = this.$createToast({
                //成功提示
                txt: res.data.msg,
                time: 3000,
                mask:true,
              });
              toast.show();
            }
          }.bind(this)
        );
    },
    validateHandler(result) {
      this.validity = result.validity;
      this.valid = result.valid;
    }
  }
};
</script>

<style lang="stylus">
.cfgf {
  background-color: #FAFAFA;
  padding-top :30px
}

.cfi {
  background-color: #ffffff;
  margin-bottom: 20px;
}

.ss {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}

.cb {
  background-color: #ffffff !important;
  color: #C0C0C0 !important;
}

.cbp {
  width: 90% !important;
  margin-left: 5% !important;
}
.whcl{
  background-color :#FAFAFA;
  padding-bottom: 30px;
  padding-top: 20px;
}


.content {
  background-color: #ffffff;
  display: flex !important;
  flex-direction: column !important;
  padding: 0px 0px !important;
  border-bottom :20px solid #fafafa !important;
}

.top {
  width: 90%;
  height: 60px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
}

.shb {
  width: 90%;
  height: 40px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 10pt;
  padding-left:5%;
  padding-right: 5%;
}

.cont {
  width: 90%;
  text-align: left;
  font-size: 10pt;
  margin: 0px 20px;
  color: #9599A0;
  line-height: 15pt;
  padding: 10px 0px;
  border-top: 2px solid #FAFAFA;
  border-bottom: 2px dashed #FAFAFA;
}

.content .title {
  color: #666666;
}

.content .cent {
  font-size: 10pt;
  color: #CCCCCC;
}

.stac {
  font-size: 10pt;
}

.nop {
  color: #FF0000;
}

.alp {
  color: #0DBC79;
}

.inp {
  color: #C66D3B;
}

.seed {
  color: #9599A0;
  font-size: 8pt;
}

.orange{
  color: #fc9153
}

.cube-toolbar-item {
    background-color: #4a4c5b !important;
}

.cube-toolbar-item .cube-btn{
  color:#ffffff !important;
}
</style>


