<script type="text/ecmascript-6">
import { DatePicker } from "cube-ui";

export default {
  data() {
    return {
      // actions: [
      //   {
      //     text: '<span class="orange">装修申请</span>',
      //     action: ""
      //   },
      //   {
      //     text: "报修申请",
      //     action: "repApply"
      //   },
      //   {
      //     text: "延长用电申请",
      //     action: "useApply"
      //   }
      // ],
      checked: false,
      validity: {},
      valid: undefined,
      model: {
        id:"",
        address: "",
        name: "",
        phone: "",
        addday: "",
        endday: "",
        workname: "",
        workman: "",
        worktel: "",
        area: [],
        qita: "",
        bumen: "",
        chtime: 0,
      },
      fields: [
        {
          type: "input",
          modelKey: "address",
          label: "门面地址",
          props: {
            placeholder: "请输入门面地址",
          },
          rules: {
            required: true
          }
        },

        {
          type: "input",
          modelKey: "name",
          label: "业主名称",
          props: {
            placeholder: "请输入业主名称"
          },
          rules: {
            required: true
          }
        },

        {
          type: "input",
          modelKey: "phone",
          label: "联系电话",
          props: {
            placeholder: "请输入联系电话"
          },
          rules: {
            required: true
          }
        },

        {
          modelKey: "addday",
          label: "开工日期",
          rules: {
            required: true
          }
        },

        {
          modelKey: "endday",
          label: "完工日期",
          rules: {
            required: true
          }
        },
        {
          type: "input",
          modelKey: "workname",
          label: "施工单位",
          props: {
            placeholder: "请输入施工单位"
          },
          rules: {
            required: true
          }
        },

        {
          type: "input",
          modelKey: "workman",
          label: "施工负责人",
          props: {
            placeholder: "请输入施工负责人"
          },
          rules: {
            required: true
          }
        },

        {
          type: "input",
          modelKey: "worktel",
          label: "施工方电话",
          props: {
            placeholder: "请输入施工方电话"
          },
          rules: {
            required: true
          }
        },

        {
          type: "checkbox-group",
          modelKey: "area",
          label: "装修范围及内容",
          props: {
            options: [
              // {
              //   label: "搭建阁楼",
              //   value: "0"
              // }
            ]
          },

          rules: {
            required: true
          }
        },
        {
          type: "textarea",
          modelKey: "qita",
          label: "其他装修范围",
          // rules: {
          //   required: true
          // },
          debounce: 100,
          props: {
            maxlength: 1000,
            placeholder: '请输入其他的器的装修范围及内容',
          }
        },

        {
          type: "select",
          modelKey: "bumen",
          label: "审核部门",
          rules: {
            required: true
          },
          props: {
            options: [
              // {
              //   value: 0,
              //   text: "业务部"
              // },
            ],
            placeholder: "请选择审核部门"
          }
        }
      ],

      dlist:'',//部门列表
    };
  },
  mounted () {
    // if (!window.sessionStorage.getItem('openid')) {
    //   this.getUserInfo().then(res => {
    //     this.depList();
    //   })
    // }else{
    //   this.depList();
    // }


    
  },
  //弹窗createAPI
  created () {
    var id=this.$route.query.id
    if(id){
      this.model.id=id
    }
    console.log("接收",id)
    this.depList();

    this.subscribeDialog = this.$createSubscribeDialog()
  }, 
  methods: {
    // /**获取openid */
    // getUserInfo() {
    //   return this.axios.get("http://wxgzh.liuzhouchuyun.com/testgetUserInfo",
    //     {
    //       params: {
    //         code: this.getUrlKeys('code')
    //       }  
    //     }
    //   ).then(
    //     (res) => {
    //       console.log("userinfo:",res.data.data,"------openid:",res.data.data.openid);
    //       window.sessionStorage.setItem('openid', res.data.data.openid)
    //       return res.data.data.openid;
    //     }
    //   );
    // },
    // //拆分路径，获取code
    // getUrlKeys(variable) {
    //    var query = window.location.search.substring(1);
    //    var vars = query.split("&");
    //    for (var i=0;i<vars.length;i++) {
    //            var pair = vars[i].split("=");
    //            if(pair[0] == variable){return pair[1];}
    //    }
    //    return(false);
    // },

    //根据当前时间和随机数生成流水号
     randomNumber() {
      const now = new Date()
      let month = now.getMonth() + 1
      let day = now.getDate()
      let hour = now.getHours()
      let minutes = now.getMinutes()
      let seconds = now.getSeconds()
      return now.getFullYear().toString() + month.toString() + day + hour + minutes + seconds + (Math.round(Math.random() * 89 + 100)).toString()
     },

    //获取首页初始化信息
    depList(id){
      this.axios.post("http://wxgzh.liuzhouchuyun.com/getDeplist",
          this.qs.stringify({
              id:this.model.id
          }),
        )
        .then(
          (res)=> {
            console.log("初始化：",res.data.data);
            this.fields[8].props.options=res.data.data.alist; //范围列表赋值
            this.fields[10].props.options=res.data.data.dlist; //部门列表赋值
            if(this.model.id){
              this.model.address=res.data.data.decinfo.address; //编辑赋值地址
              this.model.name=res.data.data.decinfo.name; //编辑赋值名字
              this.model.phone=res.data.data.decinfo.phone; //编辑赋值手机
              this.model.addday=res.data.data.decinfo.addday; //编辑赋值开始时间
              this.model.endday=res.data.data.decinfo.endday; //编辑赋值结束时间
              this.model.workname=res.data.data.decinfo.workname; //编辑赋值单位
              this.model.workman=res.data.data.decinfo.workman; //编辑赋值负责人
              this.model.worktel=res.data.data.decinfo.worktel; //编辑赋值电话
              this.model.area=res.data.data.decinfo.area; //编辑赋值范围
              this.model.qita=res.data.data.decinfo.qita; //编辑赋值其他
              this.model.bumen= parseInt(res.data.data.decinfo.bumen); //编辑赋值bumen
            }
            
          }
        )
    },
    

    /**点击底部导航栏 */
    // repApply(item) {
    //   this.$router.push({path:'repair'});
    // },
    // useApply(item) {
    //   this.$router.push({path:'useele'});
    // },
    // clickHandler(item) {
    //   if (item.action) {
    //     this[item.action](item);
    //   }
    // },

    // 日期选择器
    showDatePicker(chtime) {
      console.log(this);
      this.model.chtime = chtime;
      if (!this.datePicker) {
        this.datePicker = this.$createDatePicker({
          title: "日期选择",
          value: new Date(),
          onSelect: (selectedTime, selectedText, formatedTime) => {
            if (this.model.chtime == 0) {
              this.model.addday = selectedText.join("-");
            } else {
              this.model.endday = selectedText.join("-");
            }
          },
          onCancel: this.cancelHandle
        });
      }

      this.datePicker.show();
    },

    /**注意事项 */
    showAlert() {
      // this.$router.push({ name: "Read", params: { id: "1" } });
      this.subscribeDialog.show();
    },

    //表单提交
    submitHandler(e) {
      var that=this;
      //检验
      if(that.valid==false||this.model.address==""||this.model.name==""||this.model.phone==""||this.model.workname==""||this.model.workman==""||this.model.worktel==""||this.model.addday==""||this.model.endday==""||this.model.area==""||this.model.bumen==""){  
        that.$createDialog({
          type: 'alert',
          title: '必填项不能为空',
          icon: 'cubeic-wrong',
          confirmBtn: {
            text: '确定',
          }
        }).show()
        return false;
      };
      this.axios
        .post(
          "http://wxgzh.liuzhouchuyun.com/decApply",
          this.qs.stringify({
            id:this.model.id,
            order_id:'zx'+this.randomNumber(),
            address: this.model.address,
            name: this.model.name,
            phone: this.model.phone,
            workname: this.model.workname,
            workman: this.model.workman,
            worktel: this.model.worktel,
            addday: this.model.addday,
            endday: this.model.endday,
            area: this.model.area,
            qita: this.model.qita,
            bumen: this.model.bumen,
            openid:window.sessionStorage.getItem('openid'),
          }))
        .then(
          function(res) {
            if(res.data.code==200){
              
              that.$createDialog({
                type: 'alert',
                icon: 'cubeic-alert',
                title: '提交成功，等待审核',
                confirmBtn: {
                  text: '确定',
                  active: true,
                  disabled: false,
                  href: 'javascript:;'
                },
                onConfirm: () => {
                  that.$router.go(-1);
                },
              }).show()

            }else{
              const toast = this.$createToast({
                //成功提示
                txt: res.data.msg,
                time: 3000,
                mask:true,
              });
              toast.show();
            }
          }.bind(this)
        );
    },
    validateHandler(result) {
      this.validity = result.validity;
      this.valid = result.valid;
    }
  },

  components: {
    DatePicker
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
