  background-color: #ffffff;
  margin-bottom: 20px;
}

.ss {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}

.cb {
  background-color: #ffffff !important;
  color: #C0C0C0 !important;
}

.hcbp {
  width: 90% !important;
  margin-left: 5% !important;
}

.read {
  font-size: 10pt;
  background-color: #FAFAFA;
  text-align: left;
  padding: 15px 0px 0px 7%;
  display :flex;
  flex-direction:row;
  align-items: center;
  color:#3CA0E6;
}

.hwhcl {
  background-color: #FAFAFA;
}

.orange{
  color: #fc9153
}

.cube-toolbar-item {
    background-color: #4a4c5b !important;
}

.cube-toolbar-item .cube-btn{
  color:#ffffff !important;
}

.icon-right{
  color:#FC9153;
  font-size: 14pt;
}
</style>


