<template>

    <cube-form :model="model" @validate="validateHandler" >
      <cube-form-group class="cfgs">
        <cube-form-item :field="fields[0]" class="cfi"></cube-form-item>
      </cube-form-group>

      <cube-form-group class="whcl">
        <cube-button type="button" @click="submitHandler"  class="cbp">提交巡查记录</cube-button>
      </cube-form-group>

    </cube-form>
    

</template>

<script>
export default {
  data() {
    return {
      validity: {},
      valid: undefined,
      model: {
        record: "",
        id:'',
      },
      fields: [
        {
          type: "textarea",
          modelKey: "record",
          label: "巡查记录",
          rules: {
            required: true
          },
          debounce: 100,
          props: {
            maxlength: 1000,
            placeholder: '请输入巡查情况',
          }
        },
      ],
    };
  },
  created() {
    /**接收url传过来的参数 */

    this.model.id=this.$route.params.id;
    console.log("接收",this.model.id)
  },
 
  methods: {

    //表单提交
    submitHandler(e) {
      var that=this;
      //检验
      if(that.valid==false||that.model.record==""){  
       that.$createDialog({
          type: 'alert',
          title: '必填项不能为空',
          icon: 'cubeic-wrong',
          confirmBtn: {
            text: '确定',
          }
        }).show() 
        return false;
      }


      this.axios.post(
          "http://wxgzh.liuzhouchuyun.com/supervision",
          this.qs.stringify({
            content: this.model.record,
            dec_id: this.model.id,
            openid:window.sessionStorage.getItem('openid'),
            sbumen:window.sessionStorage.getItem('own'), //缓存的所属部门
            sname:window.sessionStorage.getItem('realname') //缓存的真实姓名
          }),)
        .then(
          function(res) {
            console.log("表单提交成功",res.data);
            //控制台打印请求成功时返回的数据
            if(res.data.code==200){
              that.$createDialog({
                type: 'alert',
                title: '录入成功',
                icon: 'cubeic-right',
                confirmBtn: {
                  text: '确定',
                }
              }).show()
            }else{
              const toast = this.$createToast({
                //成功提示
                txt: res.data.msg,
                time: 3000,
                mask:true,
              });
              toast.show();
            }
          }.bind(this)
        );
    },
    validateHandler(result) {
      console.log(result);
      this.validity = result.validity;
      this.valid = result.valid;
    }
  }
};


</script>

<style lang="stylus">
.cfgs {
  background-color: #FAFAFA;
  padding-top :30px;
}

.cfi {
  background-color: #ffffff;
}

.ss {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}

.cb {
  background-color: #ffffff !important;
  color: #C0C0C0 !important;
}

.cbp {
  width: 90% !important;
  margin-left: 5% !important;
}

.whcl{
  background-color :#FAFAFA;
  padding-bottom: 30px;
  padding-top: 20px;
}

.scontent {
  width :100%;
  background-color: #ffffff;
  display: flex !important;
  flex-direction: column !important;
  padding: 0px 0px !important;
  border-bottom :20px solid #fafafa !important;
}

.top {
  width: 90%;
  height: 60px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
}

.shb {
  width: 90%;
  height: 40px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 10pt;
  padding-left: 5%;
  padding-right: 5%;
}

.cont {
  width: 90%;
  text-align: left;
  font-size: 10pt;
  margin: 0px 5%;
  color: #9599A0;
  line-height: 15pt;
  padding: 10px 0px;
  border-top: 2px solid #FAFAFA;
  border-bottom: 2px dashed #FAFAFA;
}

.scontent .title {
  color: #666666;
}

.scontent .cent {
  font-size: 10pt;
  color: #CCCCCC;
}

.stac {
  font-size: 10pt;
}

.nop {
  color: #FF0000;
}

.alp {
  color: #0DBC79;
}

.inp {
  color: #C66D3B;
}

.seed {
  color: #9599A0;
  font-size: 8pt;
}

.orange{
  color: #fc9153
}

.cube-toolbar-item {
    background-color: #4a4c5b !important;
}

.cube-toolbar-item .cube-btn{
  color:#ffffff !important;
}
</style>


