<template>
  <div class="subscribe-dialog-view">
    <cube-popup ref="popup" @mask-click="hide">
      <div class="subscribe-dialog-wrapper">
        <span class="close" @click="hide"><i class="cubeic-close"></i></span>
        <div class="scroll-list-wrap" style="overflow:auto;font-size:10pt">
          <div>
            <h2>有装修意向的业主提出装修申请，阅读装修规定如下：</h2><br>
              1、必须按柳州市安监和消防部门对公共聚集场所门面装修要求执行。<br>
              2、持申请报告书及装修项目平面图各一份向市场部递交审批。<br>
              3、审批通过后把申请表张贴在装修门面大门上备查。<br>
              4、确需改变门面结构或搭建阁楼的需向总公司工程设备科申请同意后方可施工。<br>
              5、装修期间，应注意保持环境的整洁和卫生。严禁高空抛物，装修垃圾必须袋装，并扎紧袋口，防止渗漏到排水沟内，垃圾放置于指定地点，严禁在公共场所随意堆放。装修垃圾清运由门面使用者或施工单位自行清运。<br>
              6、装修过程中使用的电工、焊工必须持证上岗，懂行业操作规程及安全常识，现场必须配备相应的灭火器材，装修现场严禁吸烟。<br>
              7、装修材料必须符合消防安全要求，电线必须套管，涂料油漆必须环保并具有防火性能，所用的材料要难燃、阻燃。严禁在门面内吸烟，动明火。<br>
              8、业主需自行搭架制作门头广告牌的须在报告中注明，获同意后方可实施，严禁乱搭乱盖，高空作业时须佩戴安全帽，在安装广告牌时底部必须留空或预留空格作为线路检查口。广告牌安装必须牢固，不得破坏门面基础设施，如需改动设施必须报市场部同意后方可施工。<br>
              9、门面装修结构安全及装修施工过程中所有的安全责任由业主自行负责，因监管不到位出现任何安全事故，由业主自行全部承担责任。<br>
              <br>
              <br>
              装修安全要求及保证<br>
              对装修人员的要求：<br>
              1、聘用的电焊工电工等需持证上岗<br>
              2、懂行业操作规范和基本安全常识<br>
              3、讲诚信不偷工减料不留安全隐患<br>
              4、遇紧急突发情况会采取相应措施<br>
              5、烧焊装修现场必备应急用灭火器<br>
              对装修材料的要求：<br>
              1、各种材料按照消防部门要求采购<br>
              2、吊顶板隔热板装饰板要难燃阻燃<br>
              3、电线穿管插座开关要与负荷匹配<br>
              4、涂料油漆不仅环保还具防火性能<br>
              5、不得使用不合格的三无伪劣商品<br>
          
          </div>
        </div>
  
      </div>
    </cube-popup>
  </div>
</template>

<script>

export default {
  name: 'subscribe-dialog',
  data(){
    return {
      items:[],
    }
  },
  mounted(){
    this.$el.addEventListener('touchmove',function(e){
      e.stopPropagation()

    },true)
    // setTimeout(() => {
    //      this.$refs.scroll.refresh();
    //   }, 1000)
    
  },
  methods: {
    show () {
      this.$refs.popup.show()
    },
    hide () {
      this.$refs.popup.hide()
    }
  },
}
</script>

<style lang="stylus" scoped>
  .subscribe-dialog-wrapper
    position: relative
    box-sizing: border-box
    max-width: 360px
    margin: 0 25px
    padding: 38px 20px 20px 20px
    background-color: #F6F6F6
    border-radius: 4px
    .close
      position: absolute
      top: 8px
      right: 8px
      font-size: 28px
      color: #A2A8AF
    .title
      font-size: 17px
      color: #222C36
    img
      height: 180px
      margin: 8px 0 5px 0
    .desc
      line-height: 20px
      font-size: 13px
      color: #C0C8D1
    .button
      display: inline-block
      width: 230px
      margin-top: 20px
      padding: 14px
      border-radius: 4px
      font-size: 15px
      background-color: #5B8FDC
    
    .scroll-list-wrap {
      height: 350px;
      border: 1px solid rgba(0, 0, 0, 0.1);
      border-radius: 5px;
      transform: rotate(0deg); 
      overflow: hidden;
    }


    
</style>




