<template>
  <div type="toolbar" title="Toolbar" class="option-demo newpad">
    <div slot="content">
      <div class="options">
        <div class="option-list">
          <cube-form :model="model" @validate="validateHandler" >
            <cube-form-group class="cfg">
            <cube-form-item :field="fields[0]" class="cfi"></cube-form-item>
            <cube-form-item :field="fields[1]" class="cfi"></cube-form-item>
            <cube-form-item :field="fields[2]" class="cfi"></cube-form-item>
            
            <cube-form-item :field="fields[3]" class="cfi"></cube-form-item>
            <cube-form-item :field="fields[4]" class="cfi"></cube-form-item>
            <cube-form-item :field="fields[5]" class="cfi"></cube-form-item>
            <cube-form-item :field="fields[6]" class="cfi"></cube-form-item>
            <cube-form-item :field="fields[7]" class="cfi"></cube-form-item>

            <cube-form-item :field="fields[8]" class="cfi"></cube-form-item>
            </cube-form-group>

            <cube-form-group class="whcl">
            <cube-button type="button" @click="submitHandler" :primary="true" class="rcbp">提交报修申请</cube-button>
            </cube-form-group>
          </cube-form>
      </div>
       </div>
      <!-- <cube-toolbar
        :actions="actions"
        @click="clickHandler">
      </cube-toolbar> -->
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //   actions: [
      //   {
      //     text: '装修申请',
      //     action: "decApply"
      //   },
      //   {
      //     text: '<span class="orange">报修申请</span>',
      //     action: ""
      //   },
      //   {
      //     text: "延长用电申请",
      //     action: "useApply"
      //   }
      // ],

      validity: {},
      valid: undefined,
      model: {
        id:"",
        address: "",
        name: "",
        phone: "",
        wumian: "",
        shuicao: "",
        xiasdao: "",
        dianlu: "",
        qita: "",
        bumen:""
      },
      fields: [
        {
          type: "input",
          modelKey: "address",
          label: "门面地址",
          props: {
            placeholder: "请输入门面地址"
          },
          rules: {
            required: true
          }
        },

        {
          type: "input",
          modelKey: "name",
          label: "业主名称",
          props: {
            placeholder: "请输入业主名称"
          },
          rules: {
            required: true
          }
        },

        {
          type: "input",
          modelKey: "phone",
          label: "联系电话",
          props: {
            placeholder: "请输入联系电话"
          },
          rules: {
            required: true
          }
        },

        {
          type: "textarea",
          modelKey: "wumian",
          label: "屋面",
          // rules: {
          //   required: true
          // },
          debounce: 100,
          props: {
            maxlength: 1000,
            placeholder: '请输入维修情况',
          }
        },
        {
          type: "textarea",
          modelKey: "shuicao",
          label: "水槽",
          // rules: {
          //   required: true
          // },
          debounce: 100,
          props: {
            maxlength: 1000,
            placeholder: '请输入维修情况',
          }
        },
        {
          type: "textarea",
          modelKey: "xiasdao",
          label: "下水道",
          // rules: {
          //   required: true
          // },
          debounce: 100,
          props: {
            maxlength: 1000,
            placeholder: '请输入维修情况',
          }
        },
        {
          type: "textarea",
          modelKey: "dianlu",
          label: "电路",
          // rules: {
          //   required: true
          // },
          debounce: 100,
          props: {
            maxlength: 1000,
            placeholder: '请输入维修情况',
          }
        },
        {
          type: "textarea",
          modelKey: "qita",
          label: "其他",
          // rules: {
          //   required: true
          // },
          debounce: 100,
          props: {
            maxlength: 1000,
            placeholder: '请输入维修情况',
          }
        },
        {
          type:'select',
          modelKey: "bumen",
          label: "审核部门",
          rules: {
            required: true
          },
          props: {
            options: [
              // {
              //   value:0,
              //   text:"业务部"
              // }
            ],
            placeholder: '请选择审核部门',
          }
        },
      ]
    };
  },
  created () {
    var id=this.$route.query.id
    if(id){
      this.model.id=id
    }
    console.log("接收",id)
    this.depList();
  },
  methods: {
    //获取初始化信息
    depList(){
      this.axios.post("http://wxgzh.liuzhouchuyun.com/getReplist",
          this.qs.stringify({
            id:this.model.id
          }),
        )
        .then(
          function(res) {
            console.log("初始化：",res.data.data);
            this.fields[8].props.options=res.data.data.dlist; //部门列表赋值
            if(this.model.id){
              this.model.address=res.data.data.info.address; //编辑赋值地址
              this.model.name=res.data.data.info.name; //编辑赋值名字
              this.model.phone=res.data.data.info.phone; //编辑赋值手机
              this.model.wumian=res.data.data.info.wumian; //编辑赋值屋面
              this.model.shuicao=res.data.data.info.shuicao; //编辑赋值水槽
              this.model.xiasdao=res.data.data.info.xiasdao; //编辑赋值下水道
              this.model.dianlu=res.data.data.info.dianlu; //编辑赋值电路
              this.model.qita=res.data.data.info.qita; //编辑赋值其他
              this.model.bumen= parseInt(res.data.data.info.bumen); //编辑赋值bumen
              
            }
          }.bind(this)
        )
    },
    //根据当前时间和随机数生成流水号
    randomNumber() {
      const now = new Date()
      let month = now.getMonth() + 1
      let day = now.getDate()
      let hour = now.getHours()
      let minutes = now.getMinutes()
      let seconds = now.getSeconds()
      return now.getFullYear().toString() + month.toString() + day + hour + minutes + seconds + (Math.round(Math.random() * 89 + 100)).toString()
     },
    //导航栏跳转
    // decApply(item) {
    //   this.$router.push({path:'/'});
    // },
    // useApply(item) {
    //   this.$router.push({path:'useele'});
    // },
    // clickHandler(item) {
    //   if (item.action) {
    //     this[item.action](item);
    //   }
    // },

    //表单提交
    submitHandler(e) {
      var that=this;
      //检验
      if(that.valid==false||this.model.address==""||this.model.name==""||this.model.phone==""||this.model.bumen==""){  
        that.$createDialog({
          type: 'alert',
          title: '必填项不能为空',
          icon: 'cubeic-wrong',
          confirmBtn: {
            text: '确定',
          }
        }).show()
        return false;
      };

      
      this.axios.post( "http://wxgzh.liuzhouchuyun.com/repApply",
          this.qs.stringify({
            id:this.model.id,
            order_id:'bx'+this.randomNumber(),
            address: this.model.address,
            name: this.model.name,
            phone: this.model.phone,
            wumian: this.model.wumian,
            shuicao: this.model.shuicao,
            xiasdao: this.model.xiasdao,
            dianlu: this.model.dianlu,
            qita: this.model.qita,
            bumen: this.model.bumen,
            openid:window.sessionStorage.getItem('openid'),
          }),
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded"
            }
          }
        )
        .then(
          function(res) {
            console.log(res.data);
            //控制台打印请求成功时返回的数据
            if(res.data.code==200){
              
              that.$createDialog({
                type: 'alert',
                icon: 'cubeic-alert',
                title: '提交成功，等待审核',
                confirmBtn: {
                  text: '确定',
                  active: true,
                  disabled: false,
                  href: 'javascript:;'
                },
                onConfirm: () => {
                  that.$router.go(-1);
                },
              }).show()

            }else{
              const toast = this.$createToast({
                //成功提示
                txt: res.data.msg,
                time: 3000,
                mask:true,
              });
              toast.show();
            }
          }.bind(this)
        )
        .catch(
          function(err) {
            if (err.response) {
              console.log(err.response);
              //控制台打印错误返回的内容
            }
            //bind(this)可以不用
          }.bind(this)
        );
    },
    validateHandler(result) {
      this.validity = result.validity;
      this.valid = result.valid;
    }
  }
};
</script>

<style lang="stylus">
.newpad{
  padding-top: 30px;
  padding-bottom: 130px;
}

.cfg {
  background-color: #FAFAFA;
}

.cfi {
  background-color: #ffffff;
  margin-bottom: 20px;
}

.ss {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}

.cb {
  background-color: #ffffff !important;
  color: #C0C0C0 !important;
}

.rcbp {
  width: 90% !important;
  margin-left: 5% !important;
}

.whcl{
  background-color :#FAFAFA
}

.orange{
  color: #fc9153
}

.cube-toolbar-item {
    background-color: #4a4c5b !important;
}

.cube-toolbar-item .cube-btn{
  color:#ffffff !important;
}

</style>


