<template>

  <div class="cdcontent" >

      <div class="dtop">{{model.order_id}}</div>


      <!-- 已经审核过了 -->
      <div class="dcontpass" >
        <label>管理员{{model.gly_realname}}:
          <i v-if="model.gly_status=='1'">通过</i>
          <i v-else-if="model.gly_status=='-1'">不通过</i>
          <i v-else>未审核</i>
        </label> <br>
        <label>审核时间：</label> <i>{{model.gly_creatime}}</i> <br>
        <label>审核意见：</label> {{model.gly_remark}} <br>
      </div>

      <div class="dcontpass" >
        <label>负责人{{model.fzr_realname}}:
          <i v-if="model.fzr_status=='1'">通过</i>
          <i v-else-if="model.fzr_status=='-1'">不通过</i>
          <i v-else>未审核</i>
        </label> <br>
        <label>审核时间：</label> <i>{{model.fzr_creatime}}</i> <br>
        <label>审核意见：</label> {{model.fzr_remark}} <br>
      </div>

      <div class="dcontpass" >
        <label>工程设备科{{model.gcb_realname}}:
          <i v-if="model.gcb_status=='1'">通过</i>
          <i v-else-if="model.gcb_status=='-1'">不通过</i>
          <i v-else>未审核</i>
        </label> <br>
        <label>审核时间：</label> <i>{{model.gcb_creatime}}</i> <br>
        <label>审核意见：</label> {{model.gcb_remark}} <br>
      </div>

      


  </div>

</template>


<script>
export default {

  data() {
    return {
      model:{},
      value: '',
      placeholder: '请输入审核意见',
      maxlength: 1000,
      autofocus: true,
    };
  },
  created() {
    /**接收url传过来的参数 */
    var id=this.$route.query.id
    console.log("接收",id)

    //获取详情
    this.getInfo(id);
    
  },
  methods: {
    /*** 
     * 接收信息
    */
    getInfo(id) {
      var that=this;
      this.axios.post( "http://wxgzh.liuzhouchuyun.com/cheretails",
          this.qs.stringify({
            id: id,
          }))
        .then(
          function(res) {
            console.log("获取详情",res.data.data);
            that.model=res.data.data;

        });
    },

    /**审核 */
    showAlert(sh) {
      var that=this;
       that.axios.post("http://wxgzh.liuzhouchuyun.com/checkrep",
          that.qs.stringify({
            id: that.model.id,
            openid:window.sessionStorage.getItem('openid'), //缓存 职务 
            position:window.sessionStorage.getItem('position'), //缓存 职务 
            ifgcb:window.sessionStorage.getItem('ifgcb'), //缓存 是否工程设备科
            realname:window.sessionStorage.getItem('realname'), //缓存真实姓名
            value:that.value,
            sh: sh,
          }),
        ).then(
          function(res) {
            console.log("审核",res.data);
            //控制台打印请求成功时返回的数据
            if(res.data.code==200){
              //跳转到自己的审核记录
              
              that.$createDialog({
                type: 'confirm',
                icon: 'cubeic-alert',
                title: '完成审核',
                content: '是否跳转到已审核记录？',
                confirmBtn: {
                  text: '确定',
                  active: true,
                  disabled: false,
                  href: 'javascript:;'
                },
                cancelBtn: {
                  text: '取消',
                  active: false,
                  disabled: false,
                  href: 'javascript:;'
                },
                onConfirm: () => {
                  that.$router.push({path:'chedeced'});
                },
                onCancel: () => {
                  that.$router.go(-1)
                }
              }).show()

            }else{
              const toast = that.$createToast({
                txt: res.data.msg,
                time: 3000,
                type:'error',
                mask:true,
              })
              toast.show();
            }
        });
    },

  }
};
</script>

<style lang="stylus" >

.titletop{
  text-align :left;
  width :90%;
  margin-left :5%;
  margin-bottom :20px;
}

.stac{
  font-size:10pt;
  line-height :15pt;
}

.nop{
  color: #E64340;
}

.alp{
  color: #0DBC79;
}

.inp{
  color: #C66D3B;
}

.cube-btn{
  width:90% !important;
  margin-left : 5% !important;
}


.cdcontent {
  background-color: #fafafa;
  display: flex !important;
  flex-direction: column !important;
  padding: 0px 0px !important;
  border-bottom :20px solid #fafafa !important;
}

.dtop {
  width: 90%;
  height: 60px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
}

.shb {
  width: 90%;
  height: 40px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 10pt;
  padding-left: 5%;
  padding-right: 5%;
}

.dcont {
  width: 90%;
  text-align: left;
  font-size: 10pt;
  margin: 0px 5%;
  color: #9599A0;
  line-height: 15pt;
  padding: 10px 0px;
  border-top: 2px solid #FAFAFA;
  border-bottom: 2px dashed #FAFAFA;
}

.dcontpass{
  width: 90%;
  padding: 10px 0px 10px 5%;
  border-left: 5px solid #FC9153;
  font-size :10pt;
  margin-top :20px;
  text-align:left;
  line-height :20pt;
}

.dcontpass label{
  font-weight :bold;
}

</style>


