
<template>
  <div type="toolbar" title="Toolbar" class="option-demo newpad">
    <div slot="content">
      <div class="options">
        <div class="option-list">
          <cube-form :model="model" @validate="validateHandler" >
            <cube-form-group class="cfg">
            <cube-form-item :field="fields[0]" class="cfi"></cube-form-item>
            <cube-form-item :field="fields[1]" class="cfi"></cube-form-item>
            <cube-form-item :field="fields[2]" class="cfi"></cube-form-item>
            
            <cube-form-item :field="fields[3]" class="cfi"></cube-form-item>

            <cube-form-item :field="fields[4]" class="cfi">
              <cube-button class="cb" @click="showTimePicker(0)">{{showaddtime || '请选择开始时间'}}</cube-button>
            </cube-form-item>
            <cube-form-item :field="fields[5]" class="cfi">
              <cube-button class="cb" @click="showTimePicker(1)">{{showendtime || '请选择终止时间'}}</cube-button>
            </cube-form-item>

            <cube-form-item :field="fields[6]" class="cfi"></cube-form-item>

            </cube-form-group>

            <cube-form-group class="whcl">
            <cube-button type="button" @click="submitHandler" :primary="true" class="cbp">提交延长用电申请</cube-button>
            </cube-form-group>
          </cube-form>
      </div>
       </div>
      <!-- <cube-toolbar
        :actions="actions"
        @click="clickHandler">
      </cube-toolbar> -->
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      showaddtime:'',
      showendtime:'',
      // actions: [
      //   {
      //     text: '装修申请',
      //     action: "decApply"
      //   },
      //   {
      //     text: '报修申请',
      //     action: "repApply"
      //   },
      //   {
      //     text: '<span class="orange">延长用电申请</span>',
      //     action: ""
      //   }
      // ],

      validity: {},
      valid: undefined,
      model: {
        id:"",
        address: "",
        name: "",
        phone: "",
        reason: "",
        addtime:"",
        endtime:"",
        chtime:"",
        bumen:"",
      },
      fields: [
        {
          type: "input",
          modelKey: "address",
          label: "门面地址",
          props: {
            placeholder: "请输入门面地址"
          },
          rules: {
            required: true
          }
        },

        {
          type: "input",
          modelKey: "name",
          label: "业主名称",
          props: {
            placeholder: "请输入业主名称"
          },
          rules: {
            required: true
          }
        },

        {
          type: "input",
          modelKey: "phone",
          label: "联系电话",
          props: {
            placeholder: "请输入联系电话"
          },
          rules: {
            required: true
          }
        },

        {
          type: "textarea",
          modelKey: "reason",
          label: "申请理由",
          rules: {
            required: true
          },
          debounce: 100,
          props: {
            maxlength: 1000,
            placeholder: '请输入申请理由',
          }
        },
        {
          modelKey: "addtime",
          label: "开始时间",
          rules: {
            required: true
          }
        },
        {
          modelKey: "endtime",
          label: "终止时间",
          rules: {
            required: true
          }
        },
        {
          type:'select',
          modelKey: "bumen",
          label: "审核部门",
          rules: {
            required: true
          },
          props: {
            options: [
              // {
              //   value:0,
              //   text:"业务部"
              // },
            ],
            placeholder: '请选择审核部门',
          }
        },
      ]
    };
  },
  created () {
    var id=this.$route.query.id
    if(id){
      this.model.id=id
    }
    console.log("接收",id)
    this.depList();
  },
  methods: {
    //获取初始化信息
    depList(){
      this.axios.post("http://wxgzh.liuzhouchuyun.com/getUselist",
          this.qs.stringify({
            id:this.model.id
          }),
        )
        .then(
          function(res) {
            console.log("初始化：",res.data.data);
            this.fields[6].props.options=res.data.data.dlist; //部门列表赋值
            if(this.model.id){
              this.model.address=res.data.data.info.address; //编辑赋值地址
              this.model.name=res.data.data.info.name; //编辑赋值名字
              this.model.phone=res.data.data.info.phone; //编辑赋值手机
              this.model.addtime=res.data.data.info.addtime; //编辑赋值开始时间
              this.model.endtime=res.data.data.info.endtime; //编辑赋值结束时间
              this.model.reason=res.data.data.info.reason; //编辑赋值单位
              this.model.bumen= parseInt(res.data.data.info.bumen); //编辑赋值bumen

              this.showaddtime=res.data.data.info.showAddtime; //编辑赋值开始时间
              this.showendtime=res.data.data.info.showEndtime; //编辑赋值开始时间
            }
          }.bind(this)
        )
    },
    //根据当前时间和随机数生成流水号
    randomNumber() {
      const now = new Date()
      let month = now.getMonth() + 1
      let day = now.getDate()
      let hour = now.getHours()
      let minutes = now.getMinutes()
      let seconds = now.getSeconds()
      return now.getFullYear().toString() + month.toString() + day + hour + minutes + seconds + (Math.round(Math.random() * 89 + 100)).toString()
     },
    //导航栏跳转
    // decApply(item) {
    //   this.$router.push({path:'/'});
    // },
    // repApply(item) {
    //   this.$router.push({path:'repair'});
    // },
    // clickHandler(item) {
    //   if (item.action) {
    //     this[item.action](item);
    //   }
    // },

    // 时间选择器
    showTimePicker (chtime) {
      this.model.chtime = chtime;
      this.$createTimePicker({
        showNow: true,
        minuteStep: 1,
        delay: 0,
        format:'yyyy-MM-dd HH:mm',
        day: {
          len: 1,
          filter: ['今天'],
          format: 'M月D日'
        },
        onSelect: (selectedTime, selectedText, formatedTime) => {
          // this.$createDialog({
          //   type: 'warn',
          //   title: `selected time: ${selectedTime}`,
          //   content: `selected text: ${selectedText}<br>format time: ${formatedTime}`,
          //   icon: 'cubeic-alert'
          // }).show()
          console.log("时间",selectedTime,selectedText,formatedTime)
          this.model.timeValue=selectedText;
          if (this.model.chtime == 0) {
              this.showaddtime = selectedText; //显示的时间
              this.model.addtime = selectedTime; //作为参数的时间
            } else {
              this.showendtime = selectedText;
              this.model.endtime = selectedTime;
          }
        },
      }).show()
    },

    //表单提交
    submitHandler(e) {
      var that=this;
      //检验
      if(that.valid==false||this.model.address==""||this.model.name==""||this.model.phone==""||this.model.reason==""||this.model.addtime==""||this.model.endtime==""||this.model.bumen==""){  
        that.$createDialog({
          type: 'alert',
          title: '必填项不能为空',
          icon: 'cubeic-wrong',
          confirmBtn: {
            text: '确定',
          }
        }).show()
        return false;
      };

      this.axios.post("http://wxgzh.liuzhouchuyun.com/useApply",
          this.qs.stringify({
            id:this.model.id,
            order_id:'yd'+this.randomNumber(),
            address: this.model.address, 
            name: this.model.name,
            phone: this.model.phone,
            reason: this.model.reason,
            addtime: this.model.addtime,
            endtime: this.model.endtime,
            bumen: this.model.bumen,
            openid:window.sessionStorage.getItem('openid'),
          }),
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded"
            }
          }
        )
        .then(
          function(res) {
            console.log(res.data);
            //控制台打印请求成功时返回的数据
            if(res.data.code==200){
              
              that.$createDialog({
                type: 'alert',
                icon: 'cubeic-alert',
                title: '提交成功，等待审核',
                confirmBtn: {
                  text: '确定',
                  active: true,
                  disabled: false,
                  href: 'javascript:;'
                },
                onConfirm: () => {
                  that.$router.go(-1);
                },
              }).show()

            }else{
              const toast = this.$createToast({
                //成功提示
                txt: res.data.msg,
                time: 3000,
                mask:true,
              });
              toast.show();
            }
          }.bind(this)
        )
        .catch(
          function(err) {
            if (err.response) {
              console.log(err.response);
              //控制台打印错误返回的内容
            }
            //bind(this)可以不用
          }.bind(this)
        );
    },
    validateHandler(result) {
      this.validity = result.validity;
      this.valid = result.valid;
    }
  }
};
</script>

<style lang="stylus">
.newpad{
  padding-top: 30px;
  padding-bottom: 130px;
}

.cfg {
  background-color: #FAFAFA;
}

.cfi {
  background-color: #ffffff;
  margin-bottom: 20px;
}

.ss {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
}

.cb {
  background-color: #ffffff !important;
  color: #C0C0C0 !important;
}

.cbp {
  width: 90% !important;
  margin-left: 5% !important;
}

.whcl{
  background-color :#FAFAFA
}

.orange{
  color: #fc9153
}

.cube-toolbar-item {
    background-color: #4a4c5b !important;
}

.cube-toolbar-item .cube-btn{
  color:#ffffff !important;
}

</style>


