<template>

  <div class="dcontent" >

      <div class="dtop" >
          <div class="nop" v-if="model.acceptance=='-1'">
            验收未通过
          </div>
          <div class="alp" v-else-if="model.acceptance=='1'">
            验收已通过
          </div>
          <div class="inp" v-else-if="model.acceptance=='0'">
            正在验收中，请耐心等待
          </div>
          <div class="nop" v-else>
            未进行验收
          </div>

          <div class="dcent">{{model.time}}</div>
      </div>

      <div class="dcont" v-if="model.acceptance=='-1'||model.acceptance=='1'">
        审核意见：{{model.glyys_remark}} <br>
                 {{model.gcbys_remark}}
      </div>

      <div class="dcont" v-if="model.acceptance=='-1'||model.acceptance=='2'">
        <cube-button class="cbbot"  @click="showAlert">申请验收</cube-button>
      </div>

  </div>

</template>


<script>
export default {

  data() {
    return {
        model:{},
    };
  },
  created() {
    /**接收url传过来的参数 */
    var _this=this
    var id=_this.$route.query.id
    console.log("接收",id)
    this.getInfo(id);
  },
  methods: {
    /*** 
     * 接收信息
    */
    getInfo(id) {
      var that=this;
      this.axios.post( "http://wxgzh.liuzhouchuyun.com/dacceptails",
          this.qs.stringify({
            id: id,
          }))
        .then(
          function(res) {
            console.log("获取详情",that,res.data.data);
            that.model=res.data.data;
        });
    },

    /**验收 */
    showAlert() {
      var that=this;
       that.axios.post("http://wxgzh.liuzhouchuyun.com/acceptance",
          that.qs.stringify({
            id: that.model.id,
          }),
        ).then(
          function(res) {
            console.log("验收",res.data);
            //控制台打印请求成功时返回的数据

            that.$createDialog({
                type: 'alert',
                icon: 'cubeic-alert',
                title: '提交成功，等待审核',
                confirmBtn: {
                  text: '确定',
                  active: true,
                  disabled: false,
                  href: 'javascript:;'
                },
                onConfirm: () => {
                  that.getInfo(that.model.id);
                },
            }).show()

        });
    },

  }
};
</script>

<style lang="stylus" >

.titletop{
  text-align :left;
  width :90%;
  margin-left :5%;
  margin-bottom :20px;
}

.stac{
  font-size:10pt;
  line-height :15pt;
}

.nop{
  color: #E64340;
}

.alp{
  color: #0DBC79;
}

.inp{
  color: #C66D3B;
}

.cube-btn{
  width:90% !important;
  margin-left : 5% !important;
}


.dcontent {
  background-color: #ffffff;
  display: flex !important;
  flex-direction: column !important;
  padding: 0px 0px !important;
  border-bottom :20px solid #fafafa !important;
}

.dtop {
  width: 90%;
  height: 60px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
}

.shb {
  width: 90%;
  height: 40px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 10pt;
  padding-left: 5%;
  padding-right: 5%;
}

.dcont {
  width: 90%;
  text-align: left;
  font-size: 10pt;
  margin: 0px 5%;
  color: #9599A0;
  line-height: 15pt;
  padding: 10px 0px;
  border-top: 2px solid #FAFAFA;
  border-bottom: 2px dashed #FAFAFA;
}

.dcontent .dcent {
  font-size: 10pt;
  color: #CCCCCC;
}
</style>


