<template>

  <div class="scroll-list-wrap" v-bind:style="styleObject">
    <cube-scroll
      ref="scroll"
      :data="articles"
      :options="options"
      @pulling-down="onPullingDown"
      @pulling-up="onPullingUp">

      <div  v-if="articles==''" class="nojl">
          暂无巡查记录
      </div>
      <div class="scontent" v-for="(item, index) in articles" :key="index">
        <div class="shb">
            <div class="title" >
              <label class="seedsli" > 巡查人：{{item.sbumen}}{{item.sname}}</label>
            </div>
            <div class="title" >
              <label class="seedsli" >{{item.time}}</label>
            </div>
        </div>
        <div class="contsli">
          记录内容：{{item.content}}
        </div>
        
        <div class="shb" v-if="openid==item.openid">
            <div class="title" >
              <cube-button :inline="true" :primary="true" @click="edit(item.id,item.content)">编辑</cube-button>
            </div>
            <div class="title" >
              <cube-button :inline="true" @click="del(item.id)">删除</cube-button>
            </div>
        </div>
      </div>

    </cube-scroll>
  </div>
    

</template>

<script>

export default {
  data() {
    return {
      openid:'',
      styleObject: {
        height:0
      },
      options: {
        pullDownRefresh: {
          threshold: 90,
          stop: 40,
          txt: '刷新成功'
        },
        pullUpLoad: {
          threshold: 0,
            txt: {
            more: '上拉加载更多...',
            noMore: '没有更多数据...'
          }
        }
      },

      model: {
        id:'',
      },
      articles: [],
      page:1,
    };
  },
  created() {
    /**接收url传过来的参数 */
    var id=this.$route.query.id;
    this.model.id=id;
    console.log("接收",id)

    //获取openid
    this.openid =window.sessionStorage.getItem('openid'),
    //获取屏幕高度
    this.styleObject.height = ((document.documentElement.clientHeight || document.body.clientHeight)-2)+'px';
    //初始化数据
    this.getInfo(1);
  },
  methods: {
    /*** 
     * 接收信息
    */
    getInfo(page) {
      var that=this;
      that.axios.post( "http://wxgzh.liuzhouchuyun.com/svlist",
          this.qs.stringify({
            dec_id: that.model.id,
            page:page
          }))
        .then(res => {
            console.log("获取",res.data.data);
            // that.articles=res.data.data;
            if(res.data.data==''&&that.$refs.scroll!=undefined){
              that.$refs.scroll.forceUpdate()
            }else{
              that.page=page+1;
              that.articles=that.articles.concat(res.data.data);
            }
            
        });
    },



    // TODO 下拉刷新
    onPullingDown: function(){
      console.log("下拉刷新");
      // 如果有最新数据，数组开头追加数据
      // this.pullData.unshift('I am new data: ' + +new Date())
      // Mock async load.
      this.articles=[];
      setTimeout(() => {
         this.getInfo(1);
      }, 1000)
    },
    //TODO 上拉加载
    onPullingUp: function() {
      console.log("上拉加载");
      // Mock async load.
      setTimeout(() => {
        this.getInfo(this.page);
      }, 1000)
    },

    /**点击编辑 */
    edit(id,content){
       this.$router.push({name:'Editsup',params:{id:id,content:content}})
    },

    /**点击删除 */
    del(id) {
       this.$createDialog({
        type: 'confirm',
        icon: 'cubeic-important',
        title: '是否确定删除该记录？',
        confirmBtn: {
          text: '确定按钮',
          active: true,
          disabled: false,
          href: 'javascript:;'
        },
        cancelBtn: {
          text: '取消',
          active: false,
          disabled: false,
          href: 'javascript:;'
        },
        onConfirm: () => {
          var that=this;
          that.axios.post("http://wxgzh.liuzhouchuyun.com/delsupervision",
              that.qs.stringify({
                  id:id
              }),
            )
            .then(res=> {
                console.log("删除：",res.data.data);
                this.$createToast({
                  type: 'warn',
                  time: 2000,
                  txt: res.data.msg
                }).show()
                setTimeout(() => {
                  this.$router.go(0)
                }, 2000)
              }
            )
        },
      }).show()
    },


  }
};
</script>

<style lang="stylus">
.nojl{
  margin-top :30px;
}

.scontent {
  width :100%;
  background-color: #ffffff;
  display: flex !important;
  flex-direction: column !important;
  padding: 0px 0px !important;
  border-top :20px solid #fafafa !important;
}

.top {
  width: 90%;
  height: 60px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
}

.shb {
  width: 90%;
  height: 40px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 10pt;
  padding-left: 5%;
  padding-right: 5%;
}

.contsli {
  width: 90%;
  text-align: left;
  font-size: 12pt;
  margin: 0px 5%;
  color: #000000;
  line-height: 15pt;
  padding: 10px 0px;
  border-top: 2px solid #FAFAFA;
  border-bottom: 2px dashed #FAFAFA;
}

.scontent .title {
  color: #666666;
}

.scontent .cent {
  font-size: 10pt;
  color: #CCCCCC;
}

.seedsli {
  color: #9599A0;
  font-size: 10pt;
}

</style>


