<template>
  <div type="toolbar" title="Toolbar" class="option-demo">
    <div slot="content"  v-bind:style="styleObject">
      <cube-scroll
      ref="scroll"
      :data="articles"
      :options="options"
      @pulling-down="onPullingDown"
      @pulling-up="onPullingUp">
      
          <div  v-if="articles==''" class="nojl">
              暂无维修反馈记录
          </div>
          <div class="content" v-for="(item, index) in articles" :key="index" >
            <div class="top" >
                <div class="title" >
                  {{item.order_id}}
                </div>
                <div class="cent">{{item.time}}</div>
            </div>
            <div class="cont" >
              业主名称：{{item.name}} <br>
              联系电话：{{item.phone}} <br>
              门面地址：{{item.address}} <br>
              屋面：{{item.wumian}} <br>
              水槽：{{item.shuicao}} <br>
              下水道：{{item.xiasdao}} <br>
              电路：{{item.dianlu}} <br>
              其他：{{item.qita}} <br>
            </div>
            <div class="shb">
                <div class="rtitle rseed" v-if="item.repend==0&&ifgcb==1"  v-on:click="decail(item.id,item.repend)">
                  录入维修记录 
                </div>
                <div class="rtitle rseedover" v-if="item.repend!=0"  v-on:click="decail(item.id,item.repend)">
                  完工查看详情  
                </div>
            </div>
          </div>
      </cube-scroll>
    </div>

    <!-- <div slot="content" v-else>
      <div class="options">
            <div class="option-list" >
          <label>
              抱歉！您没有访问权限
          </label>
        </div>
      </div>
    </div> -->

  </div>
</template>


<script>
export default {
  data() {
    return {
      page:1,
      styleObject: {
        height:0
      },
      options: {
        pullDownRefresh: {
          threshold: 90,
          stop: 40,
          txt: '刷新成功'
        },
        pullUpLoad: {
          threshold: 0,
            txt: {
            more: '上拉加载更多...',
            noMore: '没有更多数据...'
          }
        }
      },

      articles: [],
      ifyg:0,
      ifgcb:0,//是否工程部
    };
  },
  created () {
    this.ifgcb = window.sessionStorage.getItem('ifgcb'); //判断是否工程部
    //获取屏幕高度
    this.styleObject.height = ((document.documentElement.clientHeight || document.body.clientHeight)-2)+'px';
    //初始化数据
    this.depList(1);

  },
  methods: {
    //获取首页初始化信息
    depList(page){
      let that=this;
      that.axios.post("http://wxgzh.liuzhouchuyun.com/rlist",
          that.qs.stringify({
              openid:window.sessionStorage.getItem('openid'),
              page:page
          }),
        )
        .then(res=> {
            console.log("初始化：",res.data.data);
            that.ifyg=res.data.data.staff; //是否员工
            if(res.data.data.rlist==''&&that.$refs.scroll!=undefined){
              that.$refs.scroll.forceUpdate()
            }else{
              that.page=page+1;
              that.articles=that.articles.concat(res.data.data.rlist);
            }
        })

    },

    /**详情 */
    decail(id,repend) {
      this.$router.push({ path: "feedback", query: { id: id, repend:repend } });
    },

    // TODO 下拉刷新
    onPullingDown: function(){
      console.log("下拉刷新");
      // 如果有最新数据，数组开头追加数据
      // this.pullData.unshift('I am new data: ' + +new Date())
      // Mock async load.
      this.articles=[];
      setTimeout(() => {
         this.depList(1);
      }, 1000)
    },
    //TODO 上拉加载
    onPullingUp: function() {
      console.log("上拉加载");
      // Mock async load.
      setTimeout(() => {
        this.depList(this.page);
      }, 1000)
    },

  }
};
</script>

<style lang="stylus" >
.nojl{
  margin-top :30px;
}
.content {
  width :100%;
  background-color: #ffffff;
  display: flex !important;
  flex-direction: column !important;
  margin-bottom: 20px;
  padding: 0px 0px !important;
}

.top {
  width: 90%;
  height: 60px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
}

.shb {
  width: 90%;
  height: 40px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 10pt;
  padding-left: 5%;
  padding-right: 5%;
}

.cont {
  width: 90%;
  text-align: left;
  font-size: 10pt;
  margin: 0px 5%;
  color: #9599A0;
  line-height: 15pt;
  padding: 10px 0px;
  border-top: 2px solid #FAFAFA;
  border-bottom: 2px dashed #FAFAFA;
}

.content .title {
  color: #666666;
}

.rtitle{
  font-size :8pt;
  background-color :#444654;
  color: #ffffff;
  border-radius :2px;
  line-height: 22pt;
  padding: 0px 10px;
}

.content .cent {
  font-size: 10pt;
  color: #CCCCCC;
}

.stac {
  font-size: 10pt;
}

.nop {
  color: #FF0000;
}

.alp {
  color: #0DBC79;
}

.inp {
  color: #C66D3B;
}

.rseed {
  color: #ffffff;
  font-size: 8pt;
}

.rseedover{
  color: #ffffff;
  font-size: 8pt;
  background-color :#fc9153;
}

.orange {
  color: #fc9153;
}

.cube-toolbar-item {
  background-color: #4a4c5b !important;
}

.cube-toolbar-item .cube-btn {
  color: #ffffff !important;
}
</style>


