<template>
  <div type="toolbar" title="Toolbar" class="option-demo">
    <div slot="content" v-bind:style="styleObject">

      <cube-scroll
        ref="scroll"
        :data="articles"
        :options="options"
        @pulling-down="onPullingDown"
        @pulling-up="onPullingUp">

          <div  v-if="articles==''" class="nojl">
            暂无装修验收记录
          </div>
          <div class="content" v-for="(item, index) in articles" :key="index" v-on:click="decail(item.id)">
            <div class="top">
                <div class="title" >
                 {{item.order_id}}
                </div>
                <div class="cent">{{item.time}}</div>
            </div>
            <div class="cont">
              门面地址：{{item.address}}<br>
              业主名称：{{item.name}}<br>
              联系电话：{{item.phone}}<br>
              开工日期：{{item.addday}}<br>
              完工日期：{{item.endday}}<br>
              施工单位：{{item.workname}}<br>
              施工负责人：{{item.workman}}<br>
              施工方电话：{{item.worktel}}<br>
              装修范围：
              <label v-for="(item1, index) in item.area" :key="index">
                {{item1.aname}}、
              </label>
            </div>
            <div class="shb">
              进入验收
            </div>
          </div>
      </cube-scroll>

      <cube-toolbar :actions="actions" @click="clickHandler"></cube-toolbar>
       
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      page:1,
      styleObject: {
        height:0
      },
      options: {
        pullDownRefresh: {
          threshold: 90,
          stop: 40,
          txt: '刷新成功'
        },
        pullUpLoad: {
          threshold: 0,
            txt: {
            more: '上拉加载更多...',
            noMore: '没有更多数据...'
          }
        }
      },

      articles: [],

      actions: [
        {
          text: '<span class="orange">待验收</span>',
          action: ''
        },
        {
          text: '已验收',
          action: 'okCheck'
        },
        {
          text: '返回首页',
          action: 'toIndex'
        },
      ],

    };
  },
  created () {

    //获取屏幕高度
    this.styleObject.height = ((document.documentElement.clientHeight || document.body.clientHeight)-60)+'px';
    //初始化数据
    this.depList(1);

  },
  methods: {
    //点击导航栏
    clickHandler(item) {
      if (item.action) {
        this[item.action](item)
      }
    },
    okCheck(item) {
      this.$router.push({path:'acceptdeced'});
    },
    toIndex(item){
      this.$router.push({path:'/'});
    },

    

    //获取首页初始化信息
    depList(page){
      var that=this;
      that.axios.post("http://wxgzh.liuzhouchuyun.com/getAcceptdec",
          that.qs.stringify({
              openid:window.sessionStorage.getItem('openid'),
              own:window.sessionStorage.getItem('own'), //缓存所属部门
              position:window.sessionStorage.getItem('position'), //缓存 职务 
              ifgcb:window.sessionStorage.getItem('ifgcb'), //缓存 是否工程设备科
              page:page
          }),
        )
        .then(res=> {
            console.log("初始化：",res.data.data);
            if(res.data.data==''&&that.$refs.scroll!=undefined){
              that.$refs.scroll.forceUpdate()
            }else{
              that.page=page+1;
              that.articles=that.articles.concat(res.data.data);
            }
        })
    },

    
    /**点击跳转详情 */
    decail(id) {
      this.$router.push({ path: "acceptdetails", query: { id: id } });
    },

    // TODO 下拉刷新
    onPullingDown: function(){
      console.log("下拉刷新");
      // 如果有最新数据，数组开头追加数据
      // this.pullData.unshift('I am new data: ' + +new Date())
      this.articles=[];
      setTimeout(() => {
         this.depList(1);
      }, 1000)
    },
    //TODO 上拉加载
    onPullingUp: function() {
      console.log("上拉加载");
      // Mock async load.
      setTimeout(() => {
        this.depList(this.page);
      }, 1000)
    },

  }
};
</script>

<style lang="stylus" >
.nojl{
  margin-top :30px;
}

.content {
  width:100%;
  background-color: #ffffff;
  display: flex !important;
  flex-direction: column !important;
  margin-bottom: 20px;
  padding: 0px 0px !important;
}

.top {
  width: 90%;
  height: 60px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
}

.shb {
  width: 90%;
  height: 40px;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 10pt;
  padding-left: 5%;
  padding-right: 5%;
}

.cont {
  width: 90%;
  text-align: left;
  font-size: 10pt;
  margin: 0px 5%;
  color: #9599A0;
  line-height: 15pt;
  padding: 10px 0px;
  border-top: 2px solid #FAFAFA;
  border-bottom: 2px dashed #FAFAFA;
}

.content .title {
  color: #666666;
}

.content .cent {
  font-size: 10pt;
  color: #CCCCCC;
}

.stac {
  font-size: 10pt;
}

.nop {
  color: #FF0000;
}

.alp {
  color: #0DBC79;
}

.inp {
  color: #C66D3B;
}

.seed {
  color: #9599A0;
  font-size: 8pt;
}

.orange{
  color: #fc9153
}

.cube-toolbar-item {
    background-color: #4a4c5b !important;
}

.cube-toolbar-item .cube-btn{
  color:#ffffff !important;
}

.dsee{
  font-size :8pt;
  padding:8px 10px;
  background-color :#fc9153;
  color: #ffffff;
  border-radius :5px;
}
</style>


